body{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.app{
    margin: 0 auto;
    width: 500px;
    margin-top: 50px;
}
.search{
    position: relative;
}
.app input{
    width: 100%;
    height: 28px;
    top: -8px;
    left: -8px;
    width:150px;
    border:0;
    outline: 0;
    appearance: none;
}
#search_div{
    width:100%;
    height: 35px;
    background:rgb(255, 255, 255);
    border:2px solid rgb(46, 45, 45);
    border-radius: 2px;
}
.drop-down{
    display: flex;
    flex-direction: column;
    display:none;
}
.openDrop{
    display:flex;
    flex-direction: column;
}
.closeDrop{
    display:none;
}
.delete{
    position: absolute;
    top:50%;
    padding:10px;
    cursor: pointer;
    border-radius: 10px;
    right:10px;
    transform:translate(-50%, -50%);
}
.delete:hover{
    background-color: rgb(224, 222, 222);
}
.non_delete{
    display:none;
}
.down{
    position: absolute;
    top: 45%;
    right: 17px;
    cursor: pointer;
    padding: 10px;
    transform:rotate(90deg) translate(-50%, -50%);
}
.up{
    position: absolute;
    top: 0%;
    right: 0px;
    cursor: pointer;
    padding: 10px;
    transform:rotate(-90deg);
}
.option{
    padding:10px 0;
    background-color: rgb(160, 158, 158);
    cursor:pointer;
    padding-left: 10px;
}
.allOption{
    position: absolute;
    top: 16%;
    left: 10px;
    display:flex;
}
.selectOption{
    background:rgb(199, 198, 198);
    display:flex;
    margin-left: 7px;
    border-radius: 10px;
    padding:5px;
}
.closeSelectOption{
    margin-left: 5px;
    cursor: pointer;
}